import { useState } from "react";
import "./clock.css";

const Clock = () => {
  let time = new Date().toLocaleTimeString();
  const [currentTime, setCurrrentTime] = useState(time);

  const updateTime = () => {
    let time = new Date().toLocaleTimeString();
    setCurrrentTime(time);
  };

  setInterval(updateTime, 1000);

  return (
    <div className="clock">
      <h1>{currentTime}</h1>
    </div>
  );
};

export default Clock;
